.a-image {
    position: relative;

    &-loading {
        background-color: $isabelline;
        background: linear-gradient(100deg,
        rgba(255, 255, 255, 0) 40%,
        rgba(255, 255, 255, 0.5) 50%,
        rgba(255, 255, 255, 0) 60%) $isabelline;
        background-size: 200% 100%;
        background-position-x: 180%;
        animation: 1s loading ease-in-out infinite;
    }

    &-1x1 {
        @include aspect-ratio(1, 1);
    }

    &-34x17 {
        @include aspect-ratio(34, 17);
    }

    &-75x46 {
        @include aspect-ratio(75, 46);
    }

    &-257x274 {
        @include aspect-ratio(257, 274);
    }

    &-272x289 {
        @include aspect-ratio(272, 289);
    }
    
    &-605x480 {
        @include aspect-ratio(605, 480);
    }
    
    &-921x329 {
        @include aspect-ratio(921, 329);
    }
    
    &-1531x541 {
        @include aspect-ratio(1531, 541);
    }
    
    &-96x23 {
        @include aspect-ratio(96, 23);
    }

    span,
    img {
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
    }
}

@keyframes loading {
    to {
        background-position-x: -20%;
    }
}
